.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 33px 16px 16px;
  transition-duration: $change-time;

  &:hover {
    transform: scale(1.2);
  }

  &__img {
    margin-bottom: 40px;
  }

  &__product {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $main-accent;
    transition-duration: $change-time;
  }

  &:hover &__product {
    color: $product-hover;
  }

  &__code {
    margin-bottom: 16px;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $secondary;
  }

  &__reviews {
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-text {
      font-size: 10px;
      font-weight: 400;
      line-height: 14px;
      color: $main-accent;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    color: $secondary;
  }

  &__cost {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    color: $main-accent;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border: 1px solid $blue-accent;
    border-radius: 5px;
    box-sizing: border-box;
    background-color: $blue-accent;
    color: $buy-hover;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    transition-duration: $change-time;

    &:hover {
      color: $blue-accent;
      background-color: $buy-hover;
    }
  }
}
